.searchContainer {
  width: 90%;
  margin-top: 20px;
  .searchInput {
    width: 100%;
    background-color: #2c3c41;
    border: 2px solid #3c3f41;
    border-radius: 4px 4px 0 0;
    font-weight: 600;
    outline: none;
    padding: 8px;
    transition: .3s;
    color: #b9cacf;
  }
}